import React, { useState, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import {Redirect} from 'react-router-dom';
import Button from '@material-ui/core/Button';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import slack from '../images/slack_icon.svg';
import userStore from '../stores/UserStore';
import qs from 'query-string';
import axios from 'axios';
import Alert from '@material-ui/lab/Alert';
import Snackbar from '@material-ui/core/Snackbar';
import Container from '@material-ui/core/Container';
import Paper from '@material-ui/core/Paper';
import remark from 'remark'
import reactRenderer from 'remark-react'


const useStyles = makeStyles(theme => ({
    root: {
        flexGrow: 1
    },
    menuButton: {
        marginRight: theme.spacing(2)
    },
    title: {
        flexGrow: 1
    },
    slack: {
        width: 32,
        height: 32,
        marginRight: theme.spacing(2)
    },
    container: {
        marginTop: theme.spacing(2),
        marginBottom: theme.spacing(5),
        paddingTop: theme.spacing(3),
        paddingBottom: theme.spacing(3),
        paddingLeft: theme.spacing(10),
        paddingRight: theme.spacing(10)
    }
}));

const processor = remark().use(reactRenderer);

function Main() {
    const classes = useStyles();
    const [authenticated, setAuthenticated] = useState(false);
    const [severity, setSeverity] = useState('error');
    const [message, setMessage] = useState('');
    const [snackbarOpen, setSnackbarOpen] = useState(false);

    useEffect(() => {
        userStore.on('change', () => {
            setAuthenticated(userStore.isAuthenticated());
        });
        const code = qs.parse(window.location.search).code;
        if (code !== undefined) {
            axios.get('https://slack.com/api/oauth.access', {
                params: {
                    client_id: process.env.REACT_APP_OAUTH_CLIENT_ID,
                    client_secret: process.env.REACT_APP_OAUTH_CLIENT_SECRET,
                    code: code,
                    redirect_uri: process.env.REACT_APP_OAUTH_REDIRECT
                }
            }).then((response) => {
                if (response.data['ok']) {
                    const accessToken = response.data['access_token'];
                    const userId = response.data['user_id'];
                    axios.get('https://slack.com/api/users.info', {
                        params: {
                            token: accessToken,
                            user: userId
                        }
                    }).then((response) => {
                        if (response.data['ok']) {
                            userStore.setUser(response.data['user']);
                            userStore.setAccessToken(accessToken);
                            userStore.setAuthenticated(true);
                        }
                        else {
                            setSeverity('error');
                            setMessage(response.data['error']);
                            setSnackbarOpen(true);
                        }
                    }).catch((error) => {
                        setSeverity('error');
                        setMessage(error['message']);
                        setSnackbarOpen(true);
                    });
                }
                else {
                    setSeverity('error');
                    setMessage(response.data['error']);
                    setSnackbarOpen(true);
                }
            }).catch((error) => {
                setSeverity('error');
                setMessage(error['message']);
                setSnackbarOpen(true);
            });
        }
    }, []);

    /**
     * Slackのログインボタンがクリックされた場合に呼ばれます。
     */
    let handleLogin = () => {
        const clientId = process.env.REACT_APP_OAUTH_CLIENT_ID;
        const redirectUri = process.env.REACT_APP_OAUTH_REDIRECT;
        window.location = `https://slack.com/oauth/authorize?client_id=${clientId}&scope=users:read&redirect_uri=${redirectUri}`;
    };

    /**
     * スナックバーの閉じるイベント時に呼ばれます。
     *
     * @param event イベント
     * @param reason 理由
     */
    const handleCloseSnackbar = (event, reason) => {
        if ('clickaway' === reason) {
            return;
        }
        setSnackbarOpen(false);
    };

    return authenticated ? (<Redirect to='/timerecord'/>) : (
        <div className={classes.root}>
            <AppBar position="sticky">
                <Toolbar>
                    <IconButton edge="start" className={classes.menuButton} color="inherit" aria-label="menu">
                        <MenuIcon />
                    </IconButton>
                    <Typography variant="h6" className={classes.title}>
                        株式会社ベイクラフト
                    </Typography>
                    <Button color="inherit" onClick={handleLogin}>
                        <img src={slack} className={classes.slack} alt="slack" />
                        Login with Slack
                    </Button>
                </Toolbar>
            </AppBar>

            <Snackbar open={snackbarOpen} autoHideDuration={6000} onClose={handleCloseSnackbar}>
                <Alert severity={severity} variant="filled" onClose={handleCloseSnackbar}>{message}</Alert>
            </Snackbar>

            <Container maxWidth="md" className={classes.container} component={Paper}>
                <div>
                    {processor.processSync(
                        '## Slackボットモジュールについて\n' +
                        '### 出勤記録\n' +
                        '以下のような言葉を含むメッセージに反応します。\n' +
                        '\n' +
                        '* モーニング\n' +
                        '* モ〜ニング\n' +
                        '* もーにんぐ\n' +
                        '* も〜にんぐ\n' +
                        '* おっは\n' +
                        '* おは\n' +
                        '* へろ\n' +
                        '* はろ\n' +
                        '* ヘロ\n' +
                        '* ハロ\n' +
                        '* hi\n' +
                        '* hello\n' +
                        '* morning\n' +
                        '* 出勤\n' +
                        '\n' +
                        'メッセージには日時を指定することが可能です。\n' +
                        '日時を指定しない場合は現在日時で記録されます。\n' +
                        '\n' +
                        '* おはようございます 2020/1/29 9:00\n' +
                        '* おはようございます 1/29 9:00\n' +
                        '* おはようございます 1/29\n' +
                        '* おはようございます 9:00\n' +
                        '* おはようございます\n' +
                        '\n' +
                        '### 退勤記録\n' +
                        '以下のような言葉を含むメッセージに反応します。\n' +
                        '\n' +
                        '* バイ\n' +
                        '* バーイ\n' +
                        '* バ〜イ\n' +
                        '* バァイ\n' +
                        '* ばい\n' +
                        '* ばーい\n' +
                        '* ば〜い\n' +
                        '* ばぁい\n' +
                        '* おやすみ\n' +
                        '* おつ\n' +
                        '* おっー\n' +
                        '* さらば\n' +
                        '* お先\n' +
                        '* お疲\n' +
                        '* 帰\n' +
                        '* 乙\n' +
                        '* bye\n' +
                        '* night\n' +
                        '* cu\n' +
                        '* c u\n' +
                        '* cyou\n' +
                        '* c you\n' +
                        '* seeu\n' +
                        '* see u\n' +
                        '* seeyou\n' +
                        '* see you\n' +
                        '* 退勤\n' +
                        '* ごきげんよ\n' +
                        '* グッバイ\n' +
                        '* グバイ\n' +
                        '* さようなら\n' +
                        '\n' +
                        'メッセージには日時を指定することが可能です。\n' +
                        '日時を指定しない場合は現在日時で記録されます。\n' +
                        '\n' +
                        '* お疲れ様でした 2020/1/29 17:30\n' +
                        '* お疲れ様でした 1/29 17:30\n' +
                        '* お疲れ様でした 1/29\n' +
                        '* お疲れ様でした 17:30\n' +
                        '* お疲れ様でした\n' +
                        '\n' +
                        '### 休暇記録\n' +
                        '以下のような言葉を含むメッセージに反応します。\n' +
                        '\n' +
                        '* 休\n' +
                        '* やすま\n' +
                        '* やすみ\n' +
                        '* やすむ\n' +
                        '* 休暇\n' +
                        '\n' +
                        'メッセージには日付を指定することが可能です。\n' +
                        '日付を指定しない場合は現在日付で記録されます。\n' +
                        '\n' +
                        '* 休み 2020/1/29\n' +
                        '* 休み 1/29\n' +
                        '* 休み\n' +
                        '\n' +
                        'また、ダブルクォートで文字列を括ることでメモ情報を記録することができます。\n' +
                        '\n' +
                        '* 休み 1/30 "ここがメモとして記録される"\n' +
                        '* 休み "ここがメモとして記録される"'
                    ).contents}
                </div>
            </Container>
        </div>
    );
}

export default Main
